<template>
  <div class="sms-login-container">
    <div class="login-tab clearfix">
      <router-link tag="div" to="/personCenter/smsLogin" class="login-tab-item">
        <span  class="login-link">短信登录</span>
      </router-link>
      <router-link tag="div" to="/personCenter/pwdLogin" class="login-tab-item">
        <span  class="login-link">密码登录</span>
      </router-link>
    </div>
    <form action="" @submit.prevent="submit" class="comForm">

      <div class="input-wrapper">
        <input type="text" v-model="loginForm.idNum"  @focus="focus($event)" @blur="blur($event)" placeholder="身份证号">
        <i class="del" v-show="loginForm.idNum" @mousedown.stop="del('idNum')" ></i>
      </div>
      <div class="input-wrapper msgcode-wrapper clearfix">
        <input type="text" v-model="loginForm.msgCode"  @focus="focus($event)" @blur="blur($event)" placeholder="短信验证码">
        <i class="del" v-show="loginForm.msgCode" @mousedown.stop="del('msgCode')"></i>
        <span class="aboutcode" v-if="!isMessageCode" @click="getMessageCode()">发送验证码</span>
        <span class="aboutcode sendcode" v-else>已发送({{second}})</span>
      </div>
      <div class="submit-wrapper">
        <div class="error-tips" v-show="errorTips">{{errorTips}}</div>
        <input type="submit" value="登录" class="submit" :class="allValid? 'active':''" :disabled="!allValid">
      </div>
      <div class="clearfix forget-register-wrapper">
        <router-link tag="div" to="/c" class="forget-btn">
          <span  class="forget-link">忘记密码</span>
        </router-link>
        <router-link tag="div" to="/personCenter/register" class="register-btn">
          <span  class="register-link">立即注册</span>
        </router-link>
      </div>
    </form>
  </div>
</template>

<script>
import api from '@/api'
import { ValidateUtil } from '@/assets/js/global'
// import { uuid } from '@/assets/js/util'
import { getComForm } from '@/assets/js/mixins/comForm'
import { mapActions } from 'vuex'

export default {
  name: 'login',
  mixins: [getComForm],
  data () {
    return {
      isMessageCode: false,
      loginForm: {
        idNum: '',
        msgCode: ''
      }
    }
  },
  mounted () {
    this.getList()
    // var cc = uuid()
    console.log('ff')
  },
  methods: {
    getList () {
      api.getProductList().then(res => {
        console.log(12, res)
      })
    },
    getMessageCode () {
      if (!ValidateUtil.idCardRegexs(this.loginForm.idNum)) {
        this.errorTips = '身份证号格式错误'
      }
    },
    submit () {
      // else if (!ValidateUtil.idCardRegexs(this.loginForm.idNum)) {
      //     this.error = '身份证格式错误'
      //   }
      if (!this.loginForm.idNum || !this.loginForm.msgCode) {
        this.error = '输入不得为空'
      } else {
        // this.$store.dispatch('Logins', this.loginForm)
        this.Logins(this.loginForm)
      }
    },
    del (type) {
      this.loginForm[type] = ''
    },
    ...mapActions([
      'Logins'
    ])
  },
  computed: {
    allValid () {
      return this.loginForm.idNum && (this.loginForm.msgCode.length >= 4) && !this.errorTips
    }
  },
  components: {}

}
</script>
<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";
  .sms-login-container{
    .login-tab{
      height: 41px;
      padding-top:50px;
      .login-tab-item{
        float: left;
        padding-right:50px;
        font-size: 20px;
        color: #333333;
        letter-spacing: 0;
        &.router-link-active{
          .login-link{
            padding-bottom:15px;
            font-weight: bold;
            color: #333333;
            border-bottom: 3px solid #1C1D1D;
            box-sizing: border-box;
          }
        }
      }

    }
    .msgcode-wrapper{
      input{
        width:330px;
        float: left;
      }
      i.del{
        right: 110px;
      }
      .aboutcode{
        float: right;
        cursor: pointer;
        width:90px;
        height:40px;
        line-height: 40px;
        border: 1px solid #1CD7D1;
        box-sizing: border-box;
        border-radius: 2px;
        font-size: 14px;
        color: #1CD7D1;
        text-align: center;
        margin-top:38px;
      }
    }
  }

</style>
